import { mapActions, mapGetters } from 'vuex';
import AlertCircle from 'vue-material-design-icons/AlertCircle.vue';
import Star from 'vue-material-design-icons/Star.vue';
import Download from 'vue-material-design-icons/Download.vue';
import ArrowLeft from 'vue-material-design-icons/ArrowLeft.vue';
import AccountSwitch from 'vue-material-design-icons/AccountSwitch.vue';
import { NcActions, NcActionButton, NcDialog, NcEmptyContent, NcLoadingIcon } from '@nextcloud/vue';
import FetchFilesMixin from '../mixins/FetchFilesMixin.js';
import FilesSelectionMixin from '../mixins/FilesSelectionMixin.js';
import FilesListViewer from '../components/FilesListViewer.vue';
import File from '../components/File.vue';
import logger from '../services/logger.js';
import FetchFacesMixin from '../mixins/FetchFacesMixin.js';
import Vue from 'vue';
import FaceMergeForm from '../components/Faces/FaceMergeForm.vue';
export default {
  name: 'UnassignedFaces',
  components: {
    Star,
    Download,
    AlertCircle,
    ArrowLeft,
    FaceMergeForm,
    FilesListViewer,
    File,
    NcLoadingIcon,
    NcEmptyContent,
    NcActions,
    NcActionButton,
    NcDialog,
    AccountSwitch
  },
  directives: {
    focus(el) {
      Vue.nextTick(() => el.focus());
    }
  },
  mixins: [FetchFacesMixin, FetchFilesMixin, FilesSelectionMixin],
  data() {
    return {
      showMoveModal: false,
      loadingCount: 0,
      appContent: document.getElementById('app-content-vue')
    };
  },
  computed: {
    ...mapGetters(['files', 'unassignedFiles']),
    /**
     * @return {string[]} The list of files for the current faceName.
     */
    faceFileIds() {
      return this.unassignedFiles || [];
    },
    /** @type {boolean} */
    shouldFavoriteSelection() {
      // Favorite all selection if at least one file is not on the favorites.
      return this.selectedFileIds.some(fileId => this.$store.state.files.files[fileId].favorite === 0);
    }
  },
  mounted() {
    this.fetchUnassignedFaces();
  },
  methods: {
    ...mapActions(['appendFiles', 'deleteFace', 'renameFace', 'downloadFiles', 'toggleFavoriteForFiles', 'removeFilesFromFace', 'moveFilesToFace']),
    openViewer(fileId) {
      const file = this.files[fileId];
      OCA.Viewer.open({
        path: '/' + file.filename.split('/').slice(3).join('/'),
        list: this.faceFileIds.map(fileId => ({
          ...this.files[fileId],
          filename: '/' + this.files[fileId].filename.split('/').slice(3).join('/')
        })),
        loadMore: file.loadMore ? async () => await file.loadMore(true) : () => [],
        canLoop: file.canLoop
      });
    },
    async handleMove(faceName, fileIds) {
      try {
        this.loadingCount++;
        await this.moveFilesToFace({
          oldFace: null,
          faceName,
          fileIdsToMove: fileIds
        });
        this.showMoveModal = false;
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async favoriteSelection() {
      try {
        this.loadingCount++;
        await this.toggleFavoriteForFiles({
          fileIds: this.selectedFileIds,
          favoriteState: true
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async unFavoriteSelection() {
      try {
        this.loadingCount++;
        await this.toggleFavoriteForFiles({
          fileIds: this.selectedFileIds,
          favoriteState: false
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },
    async downloadSelection() {
      try {
        this.loadingCount++;
        await this.downloadFiles(this.selectedFileIds);
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    }
  }
};